"use client";

import { motion } from "framer-motion";
import { Mic2, Camera, Settings, Cpu } from "lucide-react";

const gear = [
    {
        title: "Shure SM7B",
        meta: "DYNAMIC VOCAL CHAIN",
        description: "The industry standard for broadcast audio. Flat, wide-range frequency response for exceptionally clean and natural reproduction of both music and speech.",
        icon: Mic2
    },
    {
        title: "RØDECaster Pro II",
        meta: "AUDIO PRODUCTION STUDIO",
        description: "The world's most powerful all-in-one audio production solution. Featuring ultra-low-noise, high-gain Revolution Preamps™ for studio-grade clarity.",
        icon: Settings
    },
    {
        title: "Blackmagic 4K",
        meta: "CINEMATIC VISUAL ENGINE",
        description: "Next-generation digital film cameras with 4K sensors and 13 stops of dynamic range for true cinematic performance.",
        icon: Camera
    },
    {
        title: "Managed Security",
        meta: "REDUNDANT ARCHITECTURE",
        description: "Enterprise-grade storage and redundant systems ensuring your digital content is never lost and always secure.",
        icon: Cpu
    }
];

export default function TechSchematic() {
    return (
        <section className="py-32 relative bg-background overflow-hidden border-t border-white/5">
            <div className="container px-4 mx-auto">
                <div className="flex flex-col lg:flex-row gap-16 items-center">
                    <div className="flex-1">
                        <span className="text-cyan-400 font-bold tracking-[0.4em] uppercase text-[10px] mb-6 block">Hardware Infrastructure</span>
                        <h2 className="text-5xl md:text-7xl font-black mb-8 tracking-tighter uppercase leading-[0.85]">
                            The Technical <br />
                            <span className="text-transparent bg-clip-text bg-gradient-to-r from-white to-white/40">Schematic.</span>
                        </h2>
                        <p className="text-lg text-foreground/60 leading-relaxed mb-12 max-w-xl font-light">
                            We don&apos;t compromise on signal quality. Our studio is built on the same foundations used by the world&apos;s leading broadcasters and production houses.
                        </p>

                        <div className="grid gap-6 sm:grid-cols-2">
                            {gear.map((item, index) => (
                                <motion.div
                                    key={item.title}
                                    initial={{ opacity: 0, y: 20 }}
                                    whileInView={{ opacity: 1, y: 0 }}
                                    viewport={{ once: true }}
                                    transition={{ duration: 0.5, delay: index * 0.1 }}
                                    className="p-6 rounded-3xl glass-card border-white/5 bg-white/[0.02] hover:bg-white/[0.04] transition-all group"
                                >
                                    <div className="w-10 h-10 rounded-xl bg-cyan-500/10 flex items-center justify-center mb-4 group-hover:scale-110 transition-transform">
                                        <item.icon className="w-5 h-5 text-cyan-400" />
                                    </div>
                                    <h4 className="text-lg font-bold mb-1 uppercase tracking-tight">{item.title}</h4>
                                    <span className="text-[9px] font-bold text-cyan-500/50 uppercase tracking-widest block mb-3">{item.meta}</span>
                                    <p className="text-sm text-foreground/40 leading-relaxed font-light line-clamp-3 group-hover:text-foreground/60 transition-colors">
                                        {item.description}
                                    </p>
                                </motion.div>
                            ))}
                        </div>
                    </div>

                    <div className="flex-1 w-full relative">
                        <div className="aspect-square relative rounded-[3rem] overflow-hidden border border-white/10 glass-card p-4">
                            <div className="absolute inset-0 bg-gradient-to-br from-cyan-500/10 to-transparent" />
                            <div className="relative w-full h-full rounded-[2.5rem] overflow-hidden bg-slate-950 flex items-center justify-center border border-white/5">
                                {/* Simulated Technical Drawing */}
                                <div className="absolute inset-0 opacity-20 pointer-events-none">
                                    <div className="absolute inset-0" style={{ backgroundImage: 'radial-gradient(circle at 2px 2px, rgba(255,255,255,0.05) 1px, transparent 0)', backgroundSize: '24px 24px' }} />
                                </div>
                                <Mic2 className="w-48 h-48 text-cyan-500/20 animate-pulse" />

                                {/* Label Callouts */}
                                <motion.div
                                    animate={{ y: [0, -10, 0] }}
                                    transition={{ duration: 4, repeat: Infinity, ease: "easeInOut" }}
                                    className="absolute top-1/4 right-1/4 p-3 glass-card border-cyan-500/20 bg-slate-900/80 rounded-xl"
                                >
                                    <span className="text-[8px] font-bold text-cyan-400 uppercase tracking-widest">96kHz MASTERING</span>
                                </motion.div>
                                <motion.div
                                    animate={{ y: [0, 10, 0] }}
                                    transition={{ duration: 5, repeat: Infinity, ease: "easeInOut", delay: 1 }}
                                    className="absolute bottom-1/3 left-1/4 p-3 glass-card border-cyan-500/20 bg-slate-900/80 rounded-xl"
                                >
                                    <span className="text-[8px] font-bold text-cyan-400 uppercase tracking-widest">SUB-ZERO NOISE FLOOR</span>
                                </motion.div>
                            </div>
                        </div>

                        {/* Ambient Glow */}
                        <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-full h-full bg-cyan-500/10 rounded-full blur-[120px] pointer-events-none -z-10" />
                    </div>
                </div>
            </div>
        </section>
    );
}
